<template>
  <div class="wrapper">
    <header>账户信息</header>
    <div class="table-box">
      <div class="table-title">
        <span class="leftline"></span>
        基本信息
      </div>
    </div>
    <div class="table">
      <div class="content">
        <span>登录账号：{{loginAccount}}</span>
      </div>
      <div class="content">
        <span>联系人：{{contacts}}</span>
      </div>
      <div class="content">
        <span>联系电话：{{phone}}</span>
      </div>
    </div>

    <div class="table-box">
      <div class="table-title">
        <span class="leftline"></span>
        淘宝店信息
      </div>
    </div>
    <div class="table">
      <div class="content">
        <span>淘宝店面名称：{{shopName}}</span>
      </div>
      <div class="content">
        <span>绑定小二账号：{{bindAccount}}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      loginAccount: '',
      contacts: '',
      phone: '',
      shopName: '',
      bindAccount: ''
    }
  }
}
</script>

<style lang="scss" scoped>
.wrapper {
  header {
    color: #000;
    box-sizing: border-box;
    border-bottom: 1px solid #008cff;
    padding: 0 0 10px 10px;
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 20px;
  }
  .search-box {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    // border: 1px solid #a5a5a5;
    border-radius: 3px;
    padding: 0 15px;
    justify-content: space-between;
    .search-boxItem {
      margin: 10px 0;
    }
    .el-input {
      min-width: 200px;
      width: 15%;
      margin-right: 15px;
    }
    .el-select {
      margin-right: 15px;
    }
  }
  .table-box {
    .table-title {
      font-size: 16px;
      color: #333;
      font-weight: 600;
      height: 20px;
      line-height: 20px;
      display: flex;
      align-items: center;
      box-sizing: initial;
      padding: 10px;
      .leftline {
        display: block;
        height: 100%;
        width: 3px;
        background: #333;
        margin-right: 5px;
      }
    }
    .count-box {
      margin-top: 15px;
      display: flex;
      align-items: center;
      font-size: 14px;
      .box {
        padding: 0 30px;
        .title {
          font-weight: 600;
          color: #333;
          margin-bottom: 10px;
        }
        .result {
          color: #a5a5a5;
          text-align: center;
        }
      }
    }
    .table {
      margin-top: 15px;
    }
  }
  .content {
    padding: 5px 0 5px 15px;
  }
}
</style>



